.sidebar {
  width: 250px;
  background-color: #fff;
  border-right: 1px solid #e0e0e0;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  padding-top: 80px; /* To account for the header */
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.05);
  overflow-y: auto;
}

.sidebar-header {
  padding: 15px 20px;
  border-bottom: 1px solid #e0e0e0;
}

.sidebar-header h3 {
  font-size: 1.2rem;
  font-weight: 600;
  color: #333;
  margin: 0;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar-menu li {
  padding: 0;
  border-bottom: 1px solid #f0f0f0;
}

.sidebar-menu li a {
  display: block;
  padding: 15px 20px;
  color: #555;
  text-decoration: none;
  transition: all 0.2s ease;
}

.sidebar-menu li a:hover {
  background-color: #f5f5f7;
  color: #333;
}

.sidebar-menu li.active a {
  background-color: #f0f0f0;
  color: #000;
  font-weight: 500;
  border-left: 4px solid #007bff;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .sidebar {
    width: 100%;
    position: relative;
    height: auto;
    padding-top: 0;
    margin-bottom: 20px;
  }
} 