/* Basic reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #f5f5f7;
  color: #333;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app-header {
  background-color: #fff;
  border-bottom: 1px solid #e0e0e0;
  padding: 20px;
  text-align: center;
  font-size: 2rem;
  font-weight: 600;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  height: 80px;
}

.app-container {
  display: flex;
  flex: 1;
  margin-top: 80px; /* To account for the fixed header */
}

.app-main {
  flex: 1;
  padding: 40px;
  margin-left: 250px; /* Width of the sidebar */
}

.video-section {
  width: 100%;
  max-width: 800px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
  padding: 20px;
}

.video-section h2 {
  margin-bottom: 20px;
  font-size: 1.5rem;
  font-weight: 500;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .app-main {
    margin-left: 0;
    padding: 20px;
  }
  
  .app-container {
    flex-direction: column;
  }
}
